<template>
    <div class="container">
        <h1>Ruta privada!</h1>
        <table class="table">
            <thead>
                <tr>
                    <th scope="col">#</th>
                    <th scope="col">Nombre</th>
                    <th scope="col">Descripción</th>
                    <th scope="col">Fecha</th>
                    <th scope="col">Acciones</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in notas" :key="index">
                    <th scope="row">{{ item._id }}</th>
                    <td>{{ item.nombre }}</td>
                    <td>{{ item.descripcion }}</td>
                    <td>{{ item.date }}</td>
                    <!-- <td>
                        <b-button class="btn-warning btn-sm mx-2"
                            @click="activarEdicion(item._id)">Actualizar</b-button>
                        <b-button class="btn-danger btn-sm mx-2" @click="eliminarTarea(item._id)">Eliminar</b-button>
                    </td> -->
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
    data() {
        return {
            notas: []
        };
    },
    computed: {
        ...mapState(["token"])
    },
    methods: {
        ...mapActions(['getTareas']),
        listarNotas() {
            let config = {
                headers: {
                    token: this.token
                }
            }

            this.axios.get('/nota', config)
                .then(res => {
                    this.notas = res.data;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        agregarNota() {
            let config = {
                headers: {
                    token: this.token
                }
            }
            this.axios.post('/nueva-nota', this.nota, config)
                .then(res => {
                    this.notas.push(res.data)
                    this.nota.nombre = '';
                    this.nota.descripcion = '';
                    this.mensaje.color = 'success';
                    this.mensaje.texto = 'Nota Agregada!';
                    this.showAlert()
                })
                .catch(e => {
                    console.log(e.response);
                    if (e.response.data.error.errors.nombre.message) {
                        this.mensaje.texto = e.response.data.error.errors.nombre.message
                    } else {
                        this.mensaje.texto = 'Error de sistema';
                    }
                    this.mensaje.color = 'danger';
                    this.showAlert()
                })
        },
    },
    created() {
        this.getTareas();
        this.listarNotas();
    }
}
</script>